import {
  getComponentFromGQL,
  getListComponentsFromGQL,
  HeroImage,
} from '@/cmsComponents';

import { Navigation } from '@/components';
import { ContactForm } from '@/pages/contact';
import { typography } from '@/theme';
import {
  ComponentIds,
  getComponentId,
  isDataType,
  ListComponentIds,
  ProductPage,
  ReferenceListGQL,
} from '@/types/cmsModels';
import { PageContext } from '@/utils/PageContext';
import styled from '@emotion/styled';
import { Button, Dialog, DialogActions } from '@mui/material';
import { graphql } from 'gatsby';

import { useState } from 'react';
interface Props {
  data: {
    contentfulPage: ProductPage;
  };
  location: Location;
}

const H2 = styled.h2`
  ${typography.h2}
  text-align: center;
  padding-bottom: 16px;
  padding-top: 16px;
`;

export default function TemplatePage({ data, location }: Props) {
  const { hero, components } = data.contentfulPage;

  const [open, setOpen] = useState<boolean>(false);
  const handleClose = () => setOpen(false);
  const handleOpen = () => {
    console.log(`open`);
    setOpen(true);
  };

  const renderComponents = Array.isArray(components)
    ? components
        .filter((component) => Object.keys(component).length > 0)
        .map((component) => {
          const componentId = getComponentId(component) as ComponentIds;

          if (
            isDataType<ReferenceListGQL>(
              componentId,
              component,
              `referenceList`,
            )
          ) {
            const validatedList = component.list.filter(
              (item) => Object.keys(item).length > 0,
            );
            const listComponentId = getComponentId(
              validatedList[0],
            ) as ListComponentIds;

            return getListComponentsFromGQL[listComponentId](validatedList);
          }

          return getComponentFromGQL[componentId](componentId, component, {
            defaultContainer: true,
          });
        })
    : undefined;
  return (
    <PageContext.Provider value={{ openContactPopUp: handleOpen }}>
      <main>
        <header>
          <Navigation location={location} transparent={!!hero} />
          {hero && <HeroImage image={hero.image.gatsbyImageData} />}
        </header>
        {renderComponents}
        <Dialog
          open={open}
          onClose={handleClose}
          aria-labelledby="Contact modal"
          aria-describedby="You can submit your personal data, so we can contact you"
        >
          <H2>Neem contact op</H2>

          <ContactForm />

          <DialogActions>
            <Button onClick={handleClose} color="primary">
              Sluiten
            </Button>
          </DialogActions>
        </Dialog>
      </main>
    </PageContext.Provider>
  );
}

export const query = graphql`
  query ($slug: String!) {
    contentfulPage(slug: { eq: $slug }) {
      title
      slug
      ...Page
    }
  }
`;
